.ocr-wrapper {
  padding: 30px 10%;
  display: -ms-flexbox;
  display: flex;
  min-height: 100%;
  width: 90%;
  max-width: 800px;
  justify-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-flow: column;
      flex-flow: column;
  font-size: 14px;
  margin: 0 auto;
}
.ocr-wrapper > div,
.ocr-wrapper textarea,
.ocr-wrapper button {
  width: 100%;
  max-width: 100%;
}

.recog-result {
  height: 200px;
  font-size: 12px;
  color: #333;
  display: block;
  margin: 30px 0 10px;
  padding: 5px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  resize: none;
  outline: none;
}
.recog-result:focus {
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
}

.btn-ocr {
  width: 100%;
  display: inline-block;
  font-size: 16px;
  padding: 6px 8px;
  margin-top: 10px;
}
.add-to-polymer {
  margin-bottom: 30px;
}
.choose-wrapper {
  width: 100%;
}



